.header {
    height: 30px;
    line-height: 30px;
    background-color: #333;
    color: #fff;
    font-size: 12px;
    .pull-right {
        a {
            position: relative;
            margin-left: 50px;
            display: inline-block;
            i {
                display: block;
                position: absolute;
                left: -40px;
                top:50%;
                margin-top: -15px;
                width: 30px;
                height: 30px;
                background: url('../images/bo.jpg') no-repeat center;
            }
            i.twI {
                background: url('../images/wei.jpg') no-repeat center;
            }
        }
    }
}
a:focus, a:hover {
    color: #FF9800;
    text-decoration: underline;
}
a {
    color: #fff;
    transition: all .2s ease-in-out;
}
