.mod {
    background-color: #f8f8f8;
    .container {
        padding: 20px 0 40px;
    }
    .mod-z {
        margin: 60px 0 40px;
        border-top: 1px solid #dddddd;
        .list-unstyled {
            width: 798px;
            margin: 0 auto;
            border: 1px solid #ddd;
            position: relative;
            margin-top: -25px;
            background-color: #f8f8f8;
            li {
                float: left;
                &.active a{
                    background-color: #41b5e6;
                    color:#fff;
                    border-left-color: #41b5e6;
                    text-decoration: none;
                }
            }
            a {
                display: block;
                width: 199px;
                line-height: 50px;
                text-align: center;
                border-left: 1px solid #ddd;
                cursor: pointer;
                color: #222;
                transition: all .2s ease-in-out;
                &:hover {
                    background-color: #41b5e6;
                    color:#fff;
                    border-left-color: #41b5e6;
                    text-decoration: none;
                }
                &:focus {
                    text-decoration: none;
                }

            }
        }
    }
    .mod-table {
        .media-default {
            opacity: 0;
            display: none;
            visibility: hidden;
            -webkit-transition: opacity .15s linear;
            -o-transition: opacity .15s linear;
            transition: opacity .15s linear;
        }
        .active {
            display: block;
            visibility: visible;
        }
        .in {
            opacity: 1;
        }
    }
    .higs {
        height: 1px;
        background: #dddddd;
        margin: 20px 0;
    }
    .media-left {
        img {
            width: 205px;
            height: 150px;
            transition: all .2s ease-in-out;
            &:hover {
                opacity: .8;
            }
        }
    }
    .media-body {
        a {
            color: #000;
            text-decoration: none;
            p {
                height: 70px;
                overflow: hidden;
            }
        }
        a:focus, a:hover {
            color: #FF9800;
            text-decoration: none;
        }
        h3 {
            margin-top: 0;
            height: 26px;
            overflow: hidden;
        }
        .media-span {
            color:#b9b9b9;
        }
    }

}
